@import url(https://fonts.googleapis.com/css2?family=Noto+Serif+KR:wght@200;300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Myeongjo&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&family=Noto+Serif+KR:wght@300&display=swap);
@charset "UTF-8";

/* reset */
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,textarea,p,blockquote,th,td,input,select,textarea,button{margin:0;padding:0}
body,button,input,select,table,textarea{font-family:'Noto Serif KR', 'AritaDotum','Malgun Gothic',Arial,sans-serif;font-size:16px;font-weight:500;color:#333;line-height:1.4;letter-spacing:-0.05em}
html{height:100%}
body{height:100%;-webkit-text-size-adjust:none}
button,fieldset,img{border:0 none}
fieldset,img{border:0}
img,li{vertical-align:top}
li,ol,ul{list-style:none}
address,em{font-style:normal}
a{color:inherit;text-decoration:none}
a:active,a:focus,a:hover{text-decoration:none}
button{border:0;background:0 0;cursor:pointer}
table{margin:0;padding:0;border:0;border-spacing:0;border-collapse: collapse;}
input,select,textarea,button{vertical-align:top}
input,select,textarea{border:0;background:0;border-radius:0;-webkit-box-shadow:none;-moz-box-shadow:none;-o-box-shadow:none}
select::-ms-expand{display:none}
textarea{resize:none;box-shadow:none;border-radius:0;-webkit-box-shadow:none;-moz-box-shadow:none;-o-box-shadow:none;}
embed,iframe,img,object,video{max-width:100%}
input[type=search]::-ms-clear,input[type=search]::-ms-reveal,input[type=text]::-ms-clear,input[type=text]::-ms-reveal{display:none;width:0;height:0}
input[type=search]::-webkit-search-cancel-button{-webkit-appearance:none}
input[type='text'],input[type='password'],input[type='submit'],input[type='search']{-webkit-appearance:none;border-radius:0;background-color:transparent}
button,input[type='button'],input[type='submit'],input[type='reset'],input[type='file']{-webkit-appearance:button;border-radius:0}
.ir-text,legend,.blind{overflow:hidden;position:absolute;border:0;width:1px;height:1px;clip:rect(1px,1px,1px,1px)}
*,*:before,*:after{box-sizing: border-box;}
.clear::after {content: '';display: block;clear:both;}

.header{position:relative}.gnb__section{position:absolute;width:100%;z-index:10;text-align:center;transition:all 0.3s}.gnb__section h1{color:#fff;font-size:50px;position:absolute;left:20px;top:10px;font-family:'Nanum Pen Script', cursive;font-weight:300}.gnb__section .gnb__list{margin-top:25px;text-align:right}.gnb__section li{display:inline-block;position:relative;padding:0 20px}.gnb__section li a{display:block;font-size:16px;color:#fff;text-align:center;padding:10px 0}.gnb__section li ul{display:none}.gnb__section :hover .services-submenu{display:block}.gnb__section.fixed{position:fixed;background-color:#fff;padding-bottom:15px}.gnb__section.fixed h1{font-size:30px;color:#212121}.gnb__section.fixed .gnb__list{margin-top:10px}.gnb__section.fixed .gnb__list a{color:#212121}

.services-submenu{width:110px;position:absolute;top:35px;left:0;list-style:none;text-align:start}.services-submenu li{background:rgba(255,255,255,0.8);cursor:pointer;width:100%}.services-submenu li:hover{background:rgba(255,255,255,0.5)}.services-submenu.clicked{display:none}.submenu-item{display:block;width:100%;height:100%;font-size:13px;text-decoration:none;color:#212121;padding:5px 0px}

.slider__wrap{position:relative}.slider__wrap .slide__text{position:absolute;left:50%;top:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);color:#fff;font-size:38px;line-height:60px;z-index:10}.swiper-slide.swiper-slide-active::before{content:'';display:block;position:absolute;left:0;bottom:0;width:100%;height:100%;z-index:11;background-color:rgba(0,0,0,0.6)}

.section01{height:500px;background-color:grey}.section02{height:500px;background-color:grey}.section03{height:500px;background-color:grey}.section04{height:500px;background-color:grey}.pubroll__list{text-align:center;padding:150px 0}.pubroll__list li{display:inline-block;width:100px;height:100px;margin:0 20px}

.footer{min-height:40vh;background-color:#38383b}

